  }
/* ESTILOS BASURA */
.searchform-wrapper {
  display: flex;
  justify-content: center;
}
.Rotatation svg {
  margin-top: 16vh;
  height: 12rem;
}
.h2-principal{
  text-align: center;
  margin-top: 2rem;
  color: rgb(1, 81, 219);
  text-shadow: 0px 1px 2px #383838;
  transition: transform .3s ease;
}

.h2-principal::after{
  content: '';
  display: block;
  width: 80px;
  height: 25px;
  margin: 2.6rem auto 0 auto; 
  background-size: 80px;
 background-image: url(./assets/img/divider.png); 
 background-repeat: no-repeat;
}

.h2-principal:hover{
  transform: scale(1.1);
}
.Rotatation {
  padding-top: 35vh;
  background-image: url("./assets/img/hero-image.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: white;
}

.bg-color-darker {
  background-color: #0a0a14 !important;
}

.navbar {
  position: absolute !important;
  width: 100%;
}
@media (max-width: 768px) {
  .menu{
    position: absolute;
    background-color: #222222;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .4s ease-in-out;
    width: 100%;
    height: 100vh;
  }
  .menu ul {
    display: inline!important;
  }
  .menu ul li a{
    font-weight: bold;
    font-size: 2rem;
    margin-top: .7rem;
  }
  .menu.in{
    top: 0;
    left: 0;
  }
  .menu.out{
    top: -1000px; 
    left: 0px;

  }
}
.navbar.active {
  background: #222222  !important;
  position: fixed !important;
  transition: all .3s ease-in-out; 
}

.tituloNav {
  margin: 0;
}

.text-services{
  max-width: 90%;
  color: white;
  margin: 0 auto;
  text-align: center;
  font-size: 17px;
  padding-bottom: 3rem;
}

  @media(min-width: 768px) {
  .text-services{width: 50%;}
  .mql-grid{grid-column: auto;}
  }

  .mql-grid{
    grid-column-start: 2;
    grid-column-end: 3;
  }
@media(max-width: 768px){
  .mql-grid{
    grid-column: auto;
  }
}
